.card {
  width: 200px;
  color: #000;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 1.5rem;
  overflow: hidden;
  gap: 0.5rem;
  text-align: center;
  box-sizing: border-box;
  height: 380px;
  transition: 0.2s;
  text-decoration: none;
}

.card:hover {
  transform: scale(105%);
}

.card img {
  box-sizing: border-box;
  width: 100%;
  height: 200px;
  padding: 15px;
}

.card .textContainer{
  width: 100%;
}

.card h2 {
  width: 100%;
  font-weight: 500;
  text-transform: uppercase;
  padding: 0 15px;
  max-height: 2rem;
  overflow: hidden;
  box-sizing: border-box;

}

.card .description {
  box-sizing: border-box;
  max-height: 3.3rem;
  color: #c9c9c9;
  overflow: hidden;
  width: 100%;
  padding: 0 15px;
}

.card .price {
  font-weight: 500;
  font-size: 1.5rem;
}

.itemDetailCard{
  width: 100%;
  max-width: 1200px;
  display: flex;
  flex-direction: column;
  align-items: start;
  border-radius: 15px;
  overflow: hidden;
  background-color: #fff;
  gap: 1rem;
}

.itemDetailCard img{
  padding: 1rem;
  min-width: 250px;
  width: 30%;
  margin: auto;
}

.itemDetailCard h2,.itemDetailCard p{
  padding: 0 1rem;
}


.itemDetailCard .price{
  font-size: 1.4rem;
  font-weight: 500;
}

@media screen and (min-width: 768px) {
  .itemDetailCard{
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 2rem;
  }
}